.about {
  padding-top: 140px;
  text-align: center;
}
.about .about-header {
  font-size: 25px;
  padding-bottom: 10px;
}
.about .about-me {
  margin: auto;
  display: block;
  width: 200px;
}
.about .about-icons,
.about .about-title {
  text-align: center;
}
.about .about-icons .about-icon,
.about .about-title .about-icon {
  margin: auto;
  display: inline-block;
  width: 12%;
  padding: 3px 2%;
}
.about .about-icons .engineer,
.about .about-title .engineer {
  width: 11.7%;
}
.about .about-icons .engineer .engineer-top-screen,
.about .about-title .engineer .engineer-top-screen {
  fill: #d1d3d4;
  stroke-width: 7px;
  stroke: #231f20;
  stroke-miterlimit: 10;
}
.about .about-icons .engineer .engineer-top-text,
.about .about-title .engineer .engineer-top-text {
  font-size: 28.25px;
  font-family: Avenir-Roman, Avenir;
}
.about .about-icons .engineer .engineer-top-text,
.about .about-title .engineer .engineer-top-text {
  fill: #231f20;
}
.about .about-icons .engineer .engineer-bottom-keyboard,
.about .about-title .engineer .engineer-bottom-keyboard {
  fill: #808285;
}
.about .about-icons .engineer:hover .engineer-top-screen,
.about .about-title .engineer:hover .engineer-top-screen {
  fill: #B29EB1;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .cookie,
.about .about-title .cookie {
  width: 9%;
}
.about .about-icons .cookie:hover .cookie-base,
.about .about-title .cookie:hover .cookie-base {
  fill: #7AA98C;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .cookie:hover .cookie-chocolate-chip,
.about .about-title .cookie:hover .cookie-chocolate-chip {
  fill: #EFF4F1;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .cookie .cookie-base,
.about .about-title .cookie .cookie-base {
  fill: #f3d7b7;
}
.about .about-icons .cookie .cookie-chocolate-chip,
.about .about-title .cookie .cookie-chocolate-chip {
  fill: #9b8579;
}
.about .about-icons .dog .dog-face,
.about .about-title .dog .dog-face {
  fill: #f8f1e1;
  stroke-miterlimit: 10;
}
.about .about-icons .dog .dog-ear,
.about .about-title .dog .dog-ear {
  fill: #ad6c55;
}
.about .about-icons .dog:hover .dog-ear,
.about .about-title .dog:hover .dog-ear {
  fill: #EED8E2;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .dog:hover .dog-face,
.about .about-title .dog:hover .dog-face {
  fill: #F9F6ED;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .journal .journal-mountain-brown,
.about .about-title .journal .journal-mountain-brown {
  fill: #9b8579;
}
.about .about-icons .journal .journal-sky,
.about .about-title .journal .journal-sky,
.about .about-icons .journal .journal-mountain-gray,
.about .about-title .journal .journal-mountain-gray,
.about .about-icons .journal .journal-land,
.about .about-title .journal .journal-land,
.about .about-icons .journal .journal-sea,
.about .about-title .journal .journal-sea {
  stroke-miterlimit: 10;
}
.about .about-icons .journal .journal-sky,
.about .about-title .journal .journal-sky {
  fill: #fff3df;
}
.about .about-icons .journal .journal-mountain-gray,
.about .about-title .journal .journal-mountain-gray {
  fill: #939598;
}
.about .about-icons .journal .journal-mountain-gray,
.about .about-title .journal .journal-mountain-gray,
.about .about-icons .journal .journal-land,
.about .about-title .journal .journal-land,
.about .about-icons .journal .journal-sea,
.about .about-title .journal .journal-sea {
  stroke-width: 0.5px;
}
.about .about-icons .journal .journal-land,
.about .about-title .journal .journal-land {
  fill: #9b8579;
}
.about .about-icons .journal .journal-sea,
.about .about-title .journal .journal-sea {
  fill: #d1d3d4;
}
.about .about-icons .journal .journal-text,
.about .about-title .journal .journal-text {
  font-size: 6.62px;
  font-family: Avenir-Roman, Avenir;
}
.about .about-icons .journal:hover .journal-sea,
.about .about-title .journal:hover .journal-sea {
  fill: #8DC4DA;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .journal:hover .journal-land,
.about .about-title .journal:hover .journal-land,
.about .about-icons .journal:hover .journal-mountain-gray,
.about .about-title .journal:hover .journal-mountain-gray {
  fill: #6BA07F;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
.about .about-icons .journal:hover .journal-mountain-brown,
.about .about-title .journal:hover .journal-mountain-brown {
  fill: #86C89F;
  -webkit-transition: fill 1s ease-out;
  transition: fill 1s ease-out;
}
