.travel {
  padding-top: 70px;
  overflow: hidden;
}
.travel .travel-description {
  padding-top: 70px;
  text-align: center;
  font-size: 20px;
}
.travel .travel-map {
  overflow: hidden;
  padding-top: 50px;
  width: 100%;
  height: 700px;
}
.ReactModal__Overlay {
  opacity: 0;
  -webkit-transition: opacity 2000ms ease-in-out;
  transition: opacity 2000ms ease-in-out;
}
.ReactModal__Overlay .travel-modal {
  border: 1px solid #f1f1f1;
  text-align: center;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 60em;
  height: 36em;
  margin-top: -18em;
  margin-left: -30em;
  background: #ffffff;
  overflow: scroll;
}
.ReactModal__Overlay .travel-modal .travel-modal-img {
  max-width: 700px;
}
.ReactModal__Overlay--after-open {
  opacity: 1;
}
.ReactModal__Overlay--before-close {
  opacity: 0;
}
